@charset "UTF-8";

@import "import/lib-fonts";
@import "base";
@import "import/typography";
@import "import/reset-css";
@import "layout";

// common
.blind { display: none; overflow: hidden; border: 0; position: relative; z-index: -1; width: 1px; height: 1px; clip: rect(1px, 1px, 1px, 1px); clip-path: inset(50%);}

.pc-only{display: block !important;}
.mo-only{display: none !important;}

.text-line{overflow: hidden; text-overflow: ellipsis; white-space: nowrap;}

.flex-end{@include flex('', 'flex-end !important');}
.flex-center{@include flex('center !important');}
.flex-start{@include flex('', 'flex-start !important');}

.ml-auto{margin-left: auto !important;}

.ui-datepicker{z-index: 1000 !important;}

//space
$position: (t:top, b:bottom, r:right, l: left);
$total: 10;
@each $abbr, $pos in $position {
  @for $i from 0 to $total {
    .m#{$abbr}-#{$i} {margin-#{$pos}: $i*10px !important;}
    .p#{$abbr}-#{$i} {padding-#{$pos}: $i*10px !important;}
  }
}
@media all and (max-width:750px){
  @each $abbr, $pos in $position {
    @for $i from 0 to $total {
      .m#{$abbr}-#{$i} {margin-#{$pos}: $i*1.39vw !important;}
      .p#{$abbr}-#{$i} {padding-#{$pos}: $i*1.39vw !important;}
    }
  }
}

//color
.c-blue{color: $c-blue !important;}
.c-red{color: $c-red2 !important;}

//text-style
.bold{font-weight: 700 !important;}

.tit-xs{font-size: 20px; font-weight: 500; color: $c-gray6;}
.tit-s{font-size: 20px; font-weight: 700; color: $c-gray6;}
.tit-m{margin: 30px 0 20px; font-size: 22px; font-weight: 700; color: $c-gray6;}
.tit-l{padding-top: 72px; padding-bottom: 72px; font-size: 28px; font-weight: 700;}

//btn-style
.btn-con{display: flex;}
.btn-con .btn + .btn{margin-left: 20px;}
.btn-line{@include flex('center'); margin-bottom: 40px; padding: 30px 0; border: 1px solid #D8D8D8; border-radius: 55px;}
.btn-icon .btn{position: relative; width: 272px; height: 52px; font-size: 20px; font-weight: 700; border-radius: 26px;}
.btn-icon .btn::before{content: ''; display: inline-block; margin-top: -3px; margin-right: 20px; width: 22px; height: 22px;}
.btn-icon .btn-notice::before{ @include bg('common/btn_notice.svg');}

.btn{
  display: inline-flex; align-items: center; justify-content: center; width: fit-content;

  &-xs{width: 80px; height: 29px; font-size: 14px; font-weight: 700; border-radius: 52px;}
  &-s{width: 120px; height: 35px; font-size: 16px; font-weight: 700; border-radius: 26px;}
  &-m{width: 128px; height: 45px; font-size: 18px; font-weight: 700; border-radius: 26px;}
  &-l{width: 145px; height: 52px; font-size: 20px; font-weight: 700; border-radius: 26px;}

  &-txt{font-size: 16px; font-weight: 200; color: #393939; border-bottom: 1px solid #393939;}

  &.wid-s{padding: 0; width: 145px; min-width: auto;}
  &.wid-m{padding: 0; width: 182px; min-width: auto;}
  &.wid-l{width: 100%;}

  &.red{color: $c-white; background-color: $c-red;}
  &.red2{color: $c-white; background-color: $c-red2;}
  &.blue{color: $c-white; background-color: $c-blue;}
  &.gray{color: $c-white; background-color: $c-gray2;}
  &.gray2{color: $c-white; background-color: $c-gray3;}
  &.gray3{color: $c-white; background-color: $c-gray8;}
}

//input
input[type="file"]{display: none;}
input::placeholder, textarea::placeholder{ font-size: inherit; color: #aaa;}

.inp-con .notice-txt{position: relative; margin-top: 30px; padding-left: 20px; font-size: 14px; font-weight: 300; line-height: 1.4; word-break: keep-all;}
.inp-con .notice-txt::before{content: '※'; position: absolute; left: 0;}

.inp{@include flex(); flex-wrap: wrap; width: 100%;}
.inp + .inp{margin-top: 20px;}

.inp .inp-label{width: 150px; font-size: 18px; font-weight: 700;  color: $c-gray6; flex-shrink: 0;}
.inp .inp-area{@include flex(); width: 100%;}
.inp .inp-area .inp-group + .inp-group{margin-left: 10px;}
.inp .inp-group{@include flex(); padding: 0 20px; width: 100%; height: 55px; font-size: 18px; border: 1px solid $c-gray2; border-radius: 8px;  background: #fff;}
.inp .inp-group input{width: 100%;}
.inp .inp-group input ~ .btn{flex-shrink: 0;}
.inp .inp-group.text-area{padding: 20px; height: 130px;}
.inp .inp-group.text-area textarea{width: 100%; height: 100%; text-align: left;}

.inp .inp-error{margin-top: 12px; font-size: 14px; color: $c-red;}
.inp .cal .btn-cal{display: inline-block; width: 20px; height: 20px; @include bg('common/ico_calendar.svg', $size: contain); flex-shrink: 0;}
.inp .cal input{text-align: center;}
.inp .search .btn-search{display: inline-block; width: 24px; height: 24px; @include bg('common/ico_search.svg', $size: 20px auto); flex-shrink: 0;}
.inp .dash{padding: 0 25px;}

.inp.col .inp-area{width: calc(100% - 150px);}
.inp.col .inp-area .inp-group{width: 100%;}
.inp.col .inp-group{width: calc(100% - 150px);}
.inp.col .inp-error{margin-left: 150px;}

//checkbox
.inp-chk{text-align: right;}
.inp-chk input{display: none;}
.inp-chk input ~ label{display: inline-flex; align-items: center;}
.inp-chk input ~ label::before{content: ''; display: inline-block; width: 20px; height: 20px; @include bg('common/ico_check.svg');}
.inp-chk input ~ label .chk-txt{margin-left: 6px; font-size: 14px;}
.inp-chk input:checked ~ label::before{background-image: url('/assets/img/common/ico_check_on.svg');}

//select
.select-wrap{z-index: 10; position: relative; @include flex(); margin-bottom: 20px;}
.select-wrap .inp-select + .inp-select{margin-left: 27px;}

.inp-select ~ .inp{margin-left: 20px;}
.inp-select{width: 186px; height: 55px; flex-shrink: 0;}
.inp-select .select{position: relative; @include flex('center'); width: 100%; height: 100%; font-size: 20px; font-weight: 700; border: 1px solid $c-gray2; border-radius: 8px; cursor: pointer; background-color: #fff; z-index: 2;}
.inp-select .select::after{content: ''; display: inline-block; margin-left: 10px; width: 24px; height: 24px; @include bg('common/ico_select_down.svg');}
.inp-select .select .selected-value{padding-right: 10px; font-weight: 700;}
.inp-select .list{display: none; position: relative; margin-top: -5px; padding: 25px 10px 20px; text-align: center; font-size: 20px; line-height: 1.8; border: 1px solid $c-gray2; border-radius: 0 0 20px 20px; z-index: 1; background: #fff;}
.inp-select .list li{border-radius: 8px; cursor: pointer;}
.inp-select .list li:hover{background: $c-blue; color: #fff;}

.inp-select.on .select{background: $c-blue; color: #fff;}
.inp-select.on .select::after{background-image: url('/assets/img/common/ico_select_down_w.svg'); transform: rotate(180deg);}
.inp-select.on .list{display: block;}

.inp-select.middle{width: 186px; height: 47px;}
.inp-select.middle .select{font-size: 18px;}
.inp-select.middle .select::after{position: absolute; right: 20px;}
.inp-select.middle .list{font-size: 18px;}

.inp-select.small{width: auto; height: 40px;}
.inp-select.small .select{padding: 0 20px; justify-content: flex-start; font-size: 18px; font-weight: 400; color: #7C7C7C;}
.inp-select.small .select::after{position: absolute; right: 20px;}
.inp-select.small .select .selected-value{color: #222;}
.inp-select.small .list{padding: 15px 10px 10px; font-size: 18px; font-weight: 400; color: #7C7C7C; background-color: #fff;}
.inp-select.small .list li:hover{background: #D8D8D8; color: #7C7C7C;}

.inp-select.small.on .select{background: #fff;}
.inp-select.small.on .select::after{background-image: url('/assets/img/common/ico_select_down.svg'); transform: rotate(180deg);}

.inp.file{flex-wrap: nowrap; width: calc(100% - 370px);}
.inp.file .inp-group{overflow: auto; max-width: 670px; height: 52px;}
.inp.file .inp-group .upload-name{@include flex(); margin-right: 20px; color: #aaa;}
.inp.file .inp-group .upload-name:last-child{margin-right: 0;}
.inp.file .inp-group .upload-name::before{content: ''; display: inline-block; margin-right: 10px; width: 18px; height: 18px; @include bg('/common/ico_image.svg');}
.inp.file .inp-group .upload-name .ico-del{margin-left: 10px; width: 12px; height: 12px; @include bg('/common/ico_del2.svg');}
.inp.file .inp-label{flex-shrink: 0; @include flex('center'); margin-right: 20px; width: 182px; height: 52px; color: #fff; font-size: 20px; font-weight: 700; background-color: #AAAAAA; border-radius: 26px;}

//search-form
.search-form{margin: 0 auto 35px; width: 100%;}
.search-form .inp-con{@include flex('center');}
.search-form .item{padding: 24px; border: 1px solid #d8d8d8;}
.search-form .item + .item{border-top: none;}
.search-form .item .inner{display: flex; justify-content: space-between; margin: 0 auto; max-width: 1150px; width: 100%;}
.search-form .inp .inp-group{width: 45%; height: 47px;}
.search-form .date-con{width: 55%;}
.search-form .data-list{overflow: hidden; display: flex; width: 33%; border-radius: 3px; border: 1px solid #d8d8d8;}
.search-form .data-list li{width: 33.3%;}
.search-form .data-list li a{display: block; width: 100%; height: 47px; line-height: 47px; text-align: center; font-size: 18px;}
.search-form .data-list li.active a{color: #fff; font-weight: 700; background-color:#0058ff;}
.search-form .inp-select{width: 20%; height: 47px;}
.search-form .inp-textarea{width: 77%;}
.search-form .inp-area .inp-group{width: 100%;}

.search-form.small .date-con{width: 45%;}
.search-form.small .data-list{width: 50%;}
.search-form.small .inp-area .inp-group{height: 39px;}

//table
.table-box{border: 1px solid #D8D8D8; border-radius: 6px;}
.table-box .no-result{@include flex('center'); height: 300px; font-size: 20px; font-weight: 700;}
.table-box table{width: 100%;}
.table-box table colgroup col:nth-child(1){width: 5%;}
.table-box table colgroup col:nth-child(2){width: 15%;}
.table-box table colgroup col:nth-child(3){width: auto;}
.table-box table colgroup col:nth-child(4){width: 15%;}
.table-box table colgroup col:nth-child(5){width: 20%;}
.table-box tr{height: 70px; text-align: center; font-size: 20px;}
.table-box tr + tr{border-top: 1px solid #D8D8D8;}
.table-box tr:hover{background-color: rgba(130, 171, 248, 0.3);}
.table-box tr:hover .num{color: $c-blue; font-weight: 700;}
.table-box tr:hover .category{color: $c-blue; font-weight: 700;}
.table-box tr:hover .txt{color: $c-blue; font-weight: 700;}
.table-box td.al-l{text-align: left;}
.table-box td.txt a{display: block; overflow: hidden; text-overflow: ellipsis; white-space: nowrap; font-weight: 500;}
.table-box .tag{@include flex('center'); margin: 0 auto; width: 132px; height: 35px; color: #fff; font-size: 16px; border-radius: 18px;}
.table-box .tag.progress{background-color: $c-blue;}
.table-box .tag.complete{background-color: $c-gray3;}

//table2
.table-box .no-result{@include flex('center'); height: 300px; font-size: 20px; font-weight: 700;}
.table-box .item + .item{border-top: 1px solid #D8D8D8;}
.table-box .item.point .num, .table-box .item.point .category, .table-box .item.point .txt{color: $c-blue; font-weight: 700;}
.table-box .item a, .table-box .item button{@include flex(); padding: 0 20px; width: 100%; height: 70px; text-align: center; font-size: 20px;}
.table-box .item a:hover, .table-box .item button:hover{background-color: rgba(130, 171, 248, 0.3);}
.table-box .item a:hover .num, .table-box .item a:hover .category, .table-box .item a:hover .txt,
.table-box .item button:hover .num, .table-box .item button:hover .category, .table-box .item button:hover .txt{font-weight: 700; color: $c-blue;}
.table-box .item p{flex-shrink: 0;}
.table-box .item p:nth-child(1){width: 5%;}
.table-box .item p:nth-child(2){width: 15%;}
.table-box .item p:nth-child(3){width: 100%; flex-shrink: 1;}
.table-box .item p:nth-child(4){width: 15%; min-width: 114px;}
.table-box .item p:nth-child(5){width: 20%;}
.table-box .item .txt{overflow: hidden; padding: 0 10px; text-overflow: ellipsis; white-space: nowrap; text-align: left; font-weight: 500;}
.table-box .item .logo img{max-width: 178px; width: 90%;}

.tag{@include flex('center'); margin: 0 auto; width: 132px; height: 35px; color: #fff; font-size: 16px; border-radius: 18px;}
.tag.progress{background-color: $c-blue;}
.tag.complete{background-color: $c-gray3;}

.paging{@include flex('center'); padding: 30px 0; border-bottom: 1px solid #D8D8D8;}
.paging.no-line{border-bottom: none;}
.paging .page-list{@include flex();}
.paging .page-list button{font-size: 16px; color: #7E84A3; border-radius: 4px;}
.paging .page-list button.on{background-color: $c-blue; color: #fff;}
.paging button{width: 36px; height: 36px;}
.paging button.prev{@include bg('common/ico_arrow_prev.svg');}
.paging button.next{@include bg('common/ico_arrow_next.svg');}

//accordion
.accordion .item button::after{content: ''; flex-shrink: 0; display: inline-block; margin-right: 25px; width: 30px; height: 30px; @include bg('common/ico_arrow_right.svg');}
.accordion .item.on button::after{transform: rotate(90deg);}
.accordion .detail{display: none; padding: 32px 75px 32px calc(20% + 20px); font-size: 20px; line-height: 1.4; border-top: 1px solid #D8D8D8;}

//tab-list
.tab-list{@include flex(); margin-bottom: 28px;}
.tab-list li{@include flex('center'); width: 142px; height: 40px; color: #fff; background-color: #AAAAAA; border-radius: 25px;}
.tab-list li.on{background-color: $c-blue;}
.tab-list li + li{margin-left: 12px;}
.tab-con{display: none;}
.tab-con.on{display: block;}

@media (max-width: 1200px) {
  .search-form .item{padding: 24px 12px;}
  .search-form .item .inp-con{width: 60%;}
  //.search-form .item .inp .inp-group{font-size: 14px;}
}

@media (max-width: 960px) {
  .pc-only{display: none !important;}
  .mo-only{display: block !important;}

  .title-wrap{justify-content: center; padding-bottom: 30px; border-bottom: 1px solid #D8D8D8;}
  .title-wrap .tit-m{margin: 0 0 60px; padding-bottom: 0; border-bottom: none;}
  .tit-m{margin: 40px 0 60px; padding-bottom: 30px; text-align: center; border-bottom: 1px solid #D8D8D8;}
  .tit-l{padding-top: 40px; padding-bottom: 40px; text-align: center;}

  .tit-m ~ .search-form.small{margin-top: -25px;}

  .btn-line{border-radius: 3px;}
  .btn-icon .btn{width: 250px; height: 42px; font-size: 16px;}
  .btn-icon .btn + .btn{margin-left: 30px;}

  .inp .inp-group{font-size: 16px;}
  .inp .inp-group.text-area{font-size: 16px;}
  .inp-select .select{font-size: 16px;}
  .inp-select .list{font-size: 16px;}
  .inp.file{width: 100%;}
  .inp.file .inp-label{font-size: 16px; height: 42px;}
  .inp.file .inp-group{max-width: initial; height: 42px;}

  .inp-select.middle .select{font-size: 16px;}
  .inp-select.middle .list{font-size: 16px;}

  .inp-select.small .select{font-size: 16px;}
  .inp-select.small .list{font-size: 16px;}

  //.search-form{margin-bottom: 0; border-top: 1px solid #d8d8d8;}
  .search-form{margin-bottom: 0;}
  .search-form .item{padding: 34px 34px 40px 34px; border: none;}
  .search-form .item .inner{display: block;}
  .search-form .item.search-item{padding-top: 0;}
  .search-form .item.search-item .inner{display: flex;}
  .search-form .item .inp-con{margin: 0 auto; width: 90%;}
  .search-form .item .inp .inp-group{font-size: 18px;}
  .search-form .inp .inp-group{width: 45%; height: 47px;}
  .search-form .data-list{margin:22px auto 0; width: 60%; border-radius: 3px;}
  .search-form .data-list li a{font-size: 16px;}
  .search-form .inp-area .inp-group{width: 100%;}

  .search-form.small{border: none;}
  .search-form.small .item{padding: 0;}
  .search-form.small .data-list{width: 90%;}

  .table-box .no-result{font-size: 16px;}
  .table-box .item a, .table-box .item button{font-size: 16px;}
  .table-box .item p:nth-child(5){width: 102px;}
  .table-box .item .logo img{width: 100%;}

  .tag{width: 90px; height: 30px; font-size: 14px; border-radius: 18px;}

  .accordion .item button::after{display: none;}
  .accordion .item.on button::after{display: none;}
  .accordion .detail{padding: 32px 75px 32px calc(20% + 20px); font-size: 16px; border-top-width: 1px;}

  .tab-list{margin-bottom: 28px;}
  .tab-list li{width: 120px; height: 40px; border-radius: 25px;}
  .tab-list li + li{margin-left: 12px;}
}

@media all and(max-width: 640px){
  .tit-xs{font-size: 2.19vw;}
  .tit-s{font-size: 3.13vw;}
  .tit-m{margin: 4.69vw 0 3.13vw; font-size: 3.44vw;}
  .tit-l{padding-top: 6.25vw; padding-bottom: 6.25vw; font-size: 4.38vw;}

  .tit-m ~ .search-form.small{margin-top: 0}

  //btn-style
  .btn-con .btn + .btn{margin-left: 3.13vw;}
  .btn-line{margin-bottom: 6.25vw; padding: 4.69vw 0; border-width: 0.16vw; border-radius: 0.47vw;}
  .btn-icon .btn{width: 39.06vw; height: 6.56vw; font-size: 2.50vw; border-radius: 4.06vw;}
  .btn-icon .btn::before{margin-top: -0.47vw; margin-right: 3.13vw; width: 3.44vw; height: 3.44vw;}

  .btn{
    &-xs{width: 12.50vw; height: 4.53vw; font-size: 2.19vw; border-radius: 8.13vw;}
    &-s{width: 18.75vw; height: 5.47vw; font-size: 2.50vw; border-radius: 4.06vw;}
    &-m{width: 20.00vw; height: 7.03vw; font-size: 2.81vw; border-radius: 4.06vw;}
    &-l{width: 22.66vw; height: 8.13vw; font-size: 3.13vw; border-radius: 4.06vw;}

    &-txt{font-size: 2.50vw; border-bottom-width: 0.16vw;}

    &.wid-s{width: 22.66vw;}
    &.wid-m{width: 28.44vw;}
  }
  .inp-con .notice-txt{margin-top: 4.69vw; padding-left: 3.13vw; font-size: 2.19vw;}
  .inp + .inp{margin-top: 3.13vw;}

  .inp .inp-label{width: 23.44vw; font-size: 2.81vw;}
  .inp .inp-area .inp-group + .inp-group{margin-left: 1.56vw;}
  .inp .inp-group{padding: 0 3.13vw; height: 8.59vw; font-size: 2.50vw; border-width: 0.16vw; border-radius: 1.25vw;}
  .inp .inp-group.text-area{padding: 3.13vw; height: 20.31vw; font-size: 2.50vw;}

  .inp .inp-error{margin-top: 1.88vw; font-size: 2.19vw;}
  .inp .cal .btn-cal{width: 6.25vw; height: 6.25vw; background-size: 3.31vw auto;}
  .inp .search .btn-search{width: 6.25vw; height: 6.25vw; background-size: 3.31vw auto;}
  .inp .dash{padding: 0 3.91vw;}

  .inp.col .inp-area{width: calc(100% - 23.44vw);}
  .inp.col .inp-group{width: calc(100% - 23.44vw);}
  .inp.col .inp-error{margin-left: 23.44vw;}

  .search-form{margin-bottom: 4.69vw;}

  //checkbox
  .inp-chk input ~ label::before{width: 3.13vw; height: 3.13vw;}
  .inp-chk input ~ label .chk-txt{margin-left: 0.94vw; font-size: 2.19vw;}

  //select
  .inp-select ~ .inp{margin-left: 3.13vw;}
  .inp-select{width: 29.06vw; height: 8.59vw;}
  .inp-select .select{font-size: 2.50vw; border-width: 0.16vw; border-radius: 1.25vw;}
  .inp-select .select::after{width: 3.75vw; height: 3.75vw;}
  .inp-select .select .selected-value{padding-right: 1.56vw;}
  .inp-select .list{margin-top: -0.78vw; padding: 3.91vw 1.56vw 3.13vw; font-size: 2.50vw; border-width: 0.16vw; border-radius: 0 0 3.13vw 3.13vw;}
  .inp-select .list li{border-radius: 1.25vw;}

  .inp-select.middle{width: 29.06vw; height: 7.34vw;}
  .inp-select.middle .select{font-size: 2.50vw;}
  .inp-select.middle .select::after{right: 3.13vw;}
  .inp-select.middle .list{font-size: 2.50vw;}

  .inp-select.small{height: 6.25vw;}
  .inp-select.small .select{padding: 0 3.13vw; font-size: 2.50vw;}
  .inp-select.small .select::after{right: 3.13vw;}
  .inp-select.small .list{padding: 2.34vw 0 1.56vw; font-size: 2.50vw;}

  .inp.file .inp-group{max-width: 104.69vw; height: 6.56vw;}
  .inp.file .inp-label{margin-right: 3.13vw; width: 28.44vw; height: 6.56vw; font-size: 2.50vw; border-radius: 4.06vw;}

  .table-box .no-result{font-size: 2.50vw;}
  .table-box .item + .item{border-top-width: 0.16vw;}
  .table-box .item a, .table-box .item button{padding: 0 3.13vw; height: 10.94vw; font-size: 2.50vw;}
  .table-box .item p:nth-child(4){min-width: 17.81vw;}
  .table-box .item p:nth-child(5){width: 15.94vw;}
  .table-box .item .txt{padding: 0 1.56vw;}

